.paragraph--type--form-50-50 {
  display: flex;
  margin: $gutter 0;
  flex-direction: column;
  @include breakpoint($bp-small) {
    flex-direction: row;
  }
  @include breakpoint($bp-med) {
    margin: $gutter;
  }

  > div {
    @include breakpoint($bp-small) {
      flex-grow: 1;
      flex-basis: 50%;
    }
  }

  .text-content {
    font-size: 16px;
    display: flex;
    flex-direction: column;
    padding: 0 0 18.5px 0;
    @include breakpoint($bp-small) {
      padding-right: 14px;
      padding-bottom: 0;
      font-size: 18px;
    }
    @include breakpoint($bp-med) {
      padding-right: 40px;
    }

    p, p * {
      font-size: 16px;
      @include breakpoint($bp-small) {
        font-size: 18px;
      }
    }
  }

  #lead-form-container {
    padding-bottom: $gutter;
    padding-top: 3.5px;
  }

  .form-content {
    padding: 0;

    @include breakpoint($bp-small) {
      padding-left: 14px;
      padding-bottom: 0;
    }
    @include breakpoint($bp-med) {
      padding: 28px 0 0 40px;
    }
    
    &.success {
      h3,
      h3 + div:not(#lead-form-container) {
        display: none;
      }
    }
  }

  .ama__h1 {
    @include type($kepler-std, $font-weight-regular);
    font-size: 28px;
    margin-bottom: 14px;
    @include breakpoint($bp-small) {
      padding-top: 28px;
      font-size: 36px;
    }
  }

  .ama__hub-card__image,
  .ama__brightcove-gallery,
  .ama__hub-hero__video {
    margin: 14px auto;
  }

  .salesforce-lead-form {
    display: flex;
    flex-wrap: wrap;
    .form-item {
      flex-grow: 1;
      flex-basis: 100%;
      margin-top: 0;
      @include breakpoint($bp-small max-width) {
        margin-bottom: calc($gutter/2);
        text-align: left;
      }
      label {
        font-size: 14px;
        text-transform: uppercase;
        color: $black;
        font-weight: normal;
        &:after {
          content: '';
        }
      }
      input {
        font-size: 18px;
        line-height: 1;
        padding: 11px 17px;
        border: 1px solid $gray-64;
        &.error {
          background: none;
        }
        &:focus-visible {
          border: 1px solid $tab-focus-blue;
        }
      }
    }
    .form-actions {
      margin: 14px 0;
      input {
        min-width: 173px;
        font-weight: normal;
        @include ama-button(0, "secondary");

        &.form-submit.ama__button {
          margin: 0;
          padding: 5px 42px;
        }
    
        &:focus-visible {
          outline: 2px solid $tab-focus-blue;
          outline-offset: 3px;
        }
      }
    }
  }

  .form-item--error-message {
    margin-top: 4px;
    margin-bottom: 0;
    border: none;
    strong {
      color: $red;
      font-size: 12px;
      font-weight: normal;
    }
  }

  .form-content.success {
    padding: 120px 0;
    @include breakpoint($bp-small) {
      padding: 92px 0;
    }
  }

  #lead-form-container {
    .success_message {
      text-align: center;
      position: relative;
      h2 {
        font-size: 38px;
        margin-bottom: 112px;
        &:after {
          content: " ";
          position: absolute;
          left: 42%;
          top: 65px;
          width: 92px;
          height: 92px;
          background: url('../images/check_confirmation.svg') no-repeat 0px 0px;
        }
      }
      p {
        padding-top: 28px;
        margin: 0 auto;
        max-width: 280px;
      }
    }
  }
}